.header_container {
  background-color: white;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  padding: 25px 150px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

ul {
  list-style: none;
  margin-right: 300px;
  padding: 0;
  orientation: horizontal;
}

li a {
  color: #213343;
  font-size: 18pt;
  text-decoration: none;
}

li {
  display: inline-block;
  margin-right: 50px; /* add spacing between items */
}

li a:hover {
  cursor: pointer;
  font-weight: bold;
  border-bottom: 1px solid #213343;
}

li#logo {
  vertical-align: middle;
}

li#logo:hover {
  cursor: pointer;
  border-bottom: none;
}

li#backToGithub {
  margin-left: 200px;
}
